import { Component, Input } from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { ValueAccessor } from '../../form-builder/value-accessor';
import { ISwitcherFormControlOptions } from './switch-form-control-options.interface';
import { IFormControlComponent } from '@helix/platform/shared/api';

@Component({
  selector: 'rx-switcher-form-control',
  templateUrl: './switch-form-control.component.html',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: SwitchFormControlComponent,
      multi: true
    }
  ],
  styleUrls: ['./switch-form-control.component.scss']
})
export class SwitchFormControlComponent extends ValueAccessor<boolean> implements IFormControlComponent {
  @Input()
  options: ISwitcherFormControlOptions;

  id: string;
}
